<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <el-button size="small" type="primary" @click="dataForm" v-if="roleOptList.indexOf('materialBudget_add')> - 1"></el-button>
      </el-row>
      <div class="space"></div>
      <!--  -->
      <el-form :inline="true" :model="search">
        <el-form-item label="" prop="budgetYear">
          <el-date-picker
              v-model="search.budgetYear"
              type="year"
              value-format="yyyy"
              placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-select size="small" v-model="search.saleZoneCode" placeholder="">
            <el-option
                v-for="(item,index) in saleZone"
                :key="index"
                :label="item.zoneName"
                :value="item.zoneCode">
            </el-option>
          </el-select>
        </el-form-item>
        <div></div>
        <el-form-item>
          <el-button size="small" type="primary" @click="getTableList"></el-button>
          <el-button size="small" @click="reset"></el-button>
          <div v-if="yearSum" style="width: auto;float: right;margin-left: 30px">{{ yearSum }}</div>
        </el-form-item>
      </el-form>
    </el-card>
    <div class="space"></div>
    <el-card>
      <!--  -->
      <el-table :data="tableList" style="width: 100%">
        <el-table-column label="" type="index" align="center">
          <template slot-scope="scope">
            {{ (search.pageNo-1)*search.pageSize + scope.$index + 1 }}
          </template>
        </el-table-column>
        <el-table-column prop="materialName" label="" align="center"></el-table-column>
        <el-table-column prop="budgetValue" label="" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.budgetValue}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="statGiven" label="" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.statGiven || 0}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="statReceived" label="" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.statReceived || 0}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="statReceived" label="" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.budgetValue - scope.row.budgetUsed || 0}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="statTime" label="" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.statTime || ""}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="budgetSeason" label="" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.budgetSeason == 1">{{"1100:00-33123:59:59"}}</span>
            <span v-if="scope.row.budgetSeason == 2">{{"4100:00-63023:59:59"}}</span>
            <span v-if="scope.row.budgetSeason == 3">{{"7100:00-93023:59:59"}}</span>
            <span v-if="scope.row.budgetSeason == 4">{{"10100:00-123123:59:59"}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="budgetYear" label="" align="center"></el-table-column>
        <el-table-column prop="saleZoneName" label="" align="center"></el-table-column>
        <el-table-column prop="statRest" label="()" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.statRest || 0}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center" width="200" v-if="roleOptList.indexOf('materialBudget_valueAdd')> - 1 || roleOptList.indexOf('materialBudget_delete')> - 1">
          <template slot-scope="scope">
            <!--                        <el-button size="mini" @click="dataForm(scope.$index, scope.row)"></el-button>-->
            <el-button size="mini" @click="addRepertory(scope.row)" type="primary" v-if="roleOptList.indexOf('materialBudget_valueAdd')> - 1"></el-button>
            <el-button type="danger" size="mini" @click="del(scope.row.id)" v-if="roleOptList.indexOf('materialBudget_delete')> - 1"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="space"></div>
      <!--  -->
      <el-pagination :small="false" background layout="total, prev, pager, next, jumper"
                     @size-change="sizeChange" @current-change="currentChange"
                     :current-page="search.pageNo" :page-size="search.pageSize"
                     :total="pagination.total">
      </el-pagination>
    </el-card>
    <el-dialog title="" width="500px" :visible.sync="form.show" @close="resetForm">
      <el-form ref="ruleForm" label-width="150px" :model="form" :rules="formRules">
        <el-form-item label="" prop="materialName">
          <el-button size="small" @click="getList(3)">{{ form.materialName ? form.materialName : "" }}</el-button>
          <el-input style="display: none;" size="small" v-model="form.materialName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="stock">
          <el-input style="width: 200px" v-model="form.stock" :precision="0" :min="0" controls-position="right" size="small"
                           disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="budgetValue">
          <el-input-number v-model="form.budgetValue" :precision="0" :min="0" :max="form.stock || 0"
                           controls-position="right"></el-input-number>
          <span style="margin-left: 10px"></span>
        </el-form-item>
        <el-form-item label="" prop="budgetSeason">
          <el-select size="small" v-model="form.budgetSeason" placeholder="">
            <el-option label="1100:00-33123:59:59" value="1"></el-option>
            <el-option label="4100:00-63023:59:59" value="2"></el-option>
            <el-option label="7100:00-93023:59:59" value="3"></el-option>
            <el-option label="10100:00-123123:59:59" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="budgetYear">
          <el-date-picker
              v-model="form.budgetYear"
              type="year"
              value-format="yyyy"
              placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="saleZoneCode">
          <el-select size="small" v-model="form.saleZoneCode" @change="handleSelectZone" placeholder="">
            <el-option
                v-for="(item,index) in saleZone"
                :key="index"
                :label="item.zoneName"
                :value="item.zoneCode">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="form.show=false"> </el-button>
                <el-button size="small" type="primary" @click="saveForm"> </el-button>
            </span>
    </el-dialog>

    <!--  -->
    <el-dialog :title="title" :visible.sync="listVisible" width="800px" @close="handleColseList">
      <el-table :data="list" border :stripe="true" style="width: 100%">
        <el-table-column prop="name" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <img :src="scope.row.pic" alt="" style="height: 60px">
          </template>
        </el-table-column>
        <el-table-column prop="stock" label="" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="selectPrize(scope.row,title,awardNo)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="params.pageNo" :page-size="params.pageSize"
                       layout="total, prev, pager, next, jumper" :total="listTotal">
        </el-pagination>
      </el-col>
      <div style="clear:both"></div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'Brand',
    data() {
      var validateBudgetValue = (rule, value, callback) => {
        if (value == '0') {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      return {
        orgHasSaleZone: sessionStorage.orgHasSaleZone,   // 
        isAllSaleZone: sessionStorage.isAllSaleZone,     // 
        saleZoneCode: sessionStorage.saleZoneCode,     // code
        roleOptList: sessionStorage.roleOptList.split(","),
        saleZone: [],
        search: {
          pageNo: 1,
          pageSize: 10,
          budgetYear: new Date().getFullYear().toString(),
          saleZoneCode: null,
          materialType: 3
        },
        pagination: { // 
          total: 0
        },
        tableList: [],
        yearSum: '',
        form: {
          show: false, // 
          id: '',
          materialName: '',
          budgetValue: '',
          materialType: 3,
          materialPoolId: null,
          budgetSeason: "",
          budgetYear: "",
          saleZoneCode: "", // code
          saleZoneName: "", // 
          stock: ""          // 
        },

        formRules: {
          materialName: [{required: true, message: '', trigger: 'change'}],
          budgetValue: [{required: true, validator: validateBudgetValue, trigger: 'blur'}],
          budgetSeason: [{required: true, message: '', trigger: 'change'}],
          budgetYear: [{required: true, message: '', trigger: 'change'}],
          saleZoneCode: [{required: true, message: '', trigger: 'change'}],
        },

        title: '',
        list: [],
        params: {
          metraFlag: '',
          pageNo: 1,
          pageSize: 10,
          status: 1
        },
        listTotal: 0,
        listVisible: false,
        awardType: [
          {
            value: 1,
            label: ''
          },
          {
            value: 6,
            label: ''
          },
          {
            value: 3,
            label: ''
          },
          {
            value: 7,
            label: ''
          },
          {
            value: 8,
            label: ''
          },
        ],
        awardNo: '',
      }
    },
    created() {
      if (this.isAllSaleZone == 1) {
        this.getSaleZone()
      } else {
        this.getSaleZone()
        this.search.saleZoneCode = this.saleZoneCode
        this.getTableList()
      }
    },
    methods: {
      // 
      getSaleZone() {
        this.$request.post('/api/saleZone/userSzList', {}, true, (res) => {
          if (res.code == '200') {
            this.saleZone = res.data || []
          }
        })
      },
      currentChange(pageNo) {
        // pageNo
        this.getTableList(event, pageNo)
      },
      sizeChange(pageSize) {
        // pageNo
        this.getTableList(event, null, pageSize)
      },
      // page = {"pageCount":, "count":}
      initPagination(page) {
        if (page) {
          this.pagination.total = page.count
        }
      },
      // 
      reset() {
        this.search = {
          pageNo: 1,
          pageSize: 10,
          budgetYear: '',
          saleZoneCode: null,
          materialType: 3
        }
        this.search.budgetYear = new Date().getFullYear().toString()
        if (this.isAllSaleZone == 1) {
          this.tableList = []
        } else {
          this.search.saleZoneCode = this.saleZoneCode
          this.getTableList()
        }
      },
      resetForm() {
        this.$refs.ruleForm.resetFields()
        this.form = {
          show: false, // 
          id: '',
          materialName: '',
          budgetValue: '',
          materialType: 3,
          materialPoolId: null,
          budgetSeason: "",
          budgetYear: "",
          saleZoneCode: "", // code
          saleZoneName: "", // 
          stock: ""          // 
        }
      },
      // 
      getTableList(_event, pageNo, pageSize) {
        let _pageNo = 1
        if (pageNo) _pageNo = pageNo
        this.search.pageNo = _pageNo
        let _pageSize = 10
        if (pageSize) _pageSize = pageSize
        this.search.pageSize = _pageSize

        this.$request.post('/api/materialBudget/list', this.search, true, (res) => {
          if (res.code == '200') {
            this.tableList = res.data.budgetList.list || []
            this.yearSum = res.data.yearSum
            this.initPagination(res.data.budgetList.page || {})
          }
        })
      },
      handleSelectZone(obj) {
        let zone = this.saleZone.find((item) => {
          return item.zoneCode == obj
        })
        console.log(zone)
        this.form.saleZoneName = zone.zoneName
      },
      dataForm(index, row) {
        this.form.id = ""
        this.form.materialName = ""
        this.form.materialPoolId = ""
        this.form.stock = ""
        if (row && row.id) {
          this.form.id = row.id
          this.form.materialName = row.materialName
          this.form.materialPoolId = row.materialPoolId
          this.form.stock = row.stock
        }
        this.form.show = true
      },
      saveForm() {
        if (this.form.id) {

        } else {
          this.$refs.ruleForm.validate(valid => {
            if (valid) {
              let params = {
                "saleZoneCode": this.form.saleZoneCode, // code
                "saleZoneName": this.form.saleZoneName, // 
                "budgetValue": this.form.budgetValue, // 
                "budgetSeason": this.form.budgetSeason, // , 1,2,3,4 
                "budgetYear": this.form.budgetYear, //
                "materialType": this.form.materialType, // , 1-; 3-; 6-;
                "materialName": this.form.materialName, // 
                "materialPoolId": this.form.materialPoolId // 
              }
              this.$request.post('/api/materialBudget/add', params, true, (res) => {
                if (res.code == '200') {
                  this.getTableList()
                  this.form.show = false
                  this.$message({type: 'success', message: '!'})
                } else {
                  this.$message.error(res.msg || res.message)
                }
              })
            }
          })
        }
      },
      addRepertory(item) {
        this.$request.post('/api/wiseqr/metra/detail', {id: item.materialPoolId, metraFlag: 'redpack'}, true, (res) => {
          if (res.ret == '200000') {
            let stock = res.data.stock
            this.$prompt(
              `<div style="line-height: 30px">${item.materialName}</div>
                         <div style="line-height: 30px">${stock}</div>
                         `,
              '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputPattern: /^\d{1,}$/,
                inputErrorMessage: '',
                dangerouslyUseHTMLString: true
              }).then(({value}) => {
              if (value == 0) return this.$message.error('0')
              if (value > stock) return this.$message.error('')
              this.$request.get('/api/materialBudget/valueAdd', {
                budgetId: item.id,
                budgetValue: value,
                materialPoolId: item.materialPoolId
              }, res1 => {
                if (res1.code == '200') {
                  this.$message.success('')
                  this.getTableList()
                } else {
                  this.$message.error(res1.msg)
                }
              })
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              })
            })
          } else {
            this.$message.error(res.msg || res.message)
          }
        })
      },
      async del(id) {
        const confirmResult = await this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        if (confirmResult !== 'confirm') {
          return this.$message({
            type: 'info',
            message: ''
          })
        }
        this.$request.get(
          '/api/materialBudget/delete',
          {budgetId: id},
          res => {
            console.log(res)
            if (res.code == '200') {
              this.$message.success('')
              this.getTableList()
            } else {
              this.$message.error(res.msg)
            }
          },
          err => {
            console.log(err)
          }
        )
      },
      // 
      selectPrize(obj, title, awardNo) {
        this.form.materialName = obj.name
        this.form.materialPoolId = obj.id
        this.form.stock = obj.stock
        console.log(obj)

        this.handleColseList()
      },
      handleColseList() {
        this.listVisible = false
        this.params.pageNo = 1
      },
      handleSizeChange(newSize) {
        this.params.pageSize = newSize
        this.getList()
      },
      handleCurrentChange(newSize) {
        this.params.pageNo = newSize
        this.getList()
      },


      getList(type, key) {
        if (type == '1') {
          this.params.metraFlag = 'object'
          this.title = ''
        } else if (type == '2') {
          this.params.metraFlag = 'virtual'
          this.title = ''
        } else if (type == '3') {
          this.params.metraFlag = 'redpack'
          this.title = ''
        } else if (type == '6') {
          this.params.metraFlag = 'integral'
          this.title = ''
        } else if (type == '7') {
          this.params.metraFlag = 'cdDisc'
          this.title = ''
        } else if (type == '8') {
          this.params.metraFlag = 'cdDouble'
          this.title = ''
        }
        if (key != undefined) {
          if (key != -1) {
            this.awardNo = key
          }
        }
        this.$request.post('/api/wiseqr/metra/list', this.params, true, res => {
          if (res.ret === '200000') {
            this.list = []
            this.list = res.data.list
            this.listTotal = res.data.page.count
            this.listVisible = true
            return
          }
          this.$message.error(res.message)
        })
      },
    }
  }
</script>

<style scoped>
  .space {
    position: relative;
    width: 100%;
    height: 20px;
  }

  .el-input, .el-select {
    width: 200px;
  }
</style>



